.main-home {

  position: relative;

  .login {

    position: absolute;
    bottom: 0%;
    left: 50%;
    transform: translate(-50%, 0%);
    z-index: 10;
    margin: -2rem 0 0 0;
    width: 100%;
    
    .container {
      background: linear-gradient(180deg, #231250, $primary-color);
      border-top-left-radius: 30px;
      border-top-right-radius: 30px;
      padding: 2rem 0;
      height: auto;
      min-height: 30vh;
    }
    
    &-title {
      
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: 0 auto;
      @include mq(phone-xl, max) {
        display: block;
        text-align: center;
        width: calc(100% - 1rem);
      }

      h1 {
        @include mq(phone-xl, max) {
          margin-top: 0;
        }
      }
      
      * { color: $white }
      
      .divider-ver {
        border: 1px solid $white;
        margin: auto 2rem;
        height: 20px;
        @include mq(phone-xl, max) {
          display: none;
        }
      }
      
    }
    
    &-form {
      
      @extend %flex-row-between;
      margin: 0 auto;
      overflow-y: hidden;

      @include mq(phone-xl, max) {
        display: block;
        margin: auto;
      }
      
      .input-group {
        @extend %flex-column-start;
        @include mq(phone-xl, max) {
          align-items: center;
        }
      }

    }
    
  }

  .steps {

    background: $primary-color;
    padding: 0 0 5rem 0;

    &-title {

      text-align: center;
      
      h1 {
        font-size: 4rem;
        letter-spacing: 10px;
        margin-bottom: 0;
      }

      * {
        color: $white;
      }

    }

    &-by {

      text-align: center;

      * { color: $white}

      h1 {
        font-size: 4rem;
      }

      h2 {
        font-size: 1rem;
        text-transform: uppercase;
      }

    }

    .step {
      &-1 {
        .step-title {
          display: flex;
          justify-content: center;
          align-items: center;

          &:nth-child(1) {
            background-image: url(../media/img/step-1.png);
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            width: 250px;
            height: 250px;
            margin: auto;
            @include mq(desk-lg, max) {
              width: 200px;
              height: 200px;              
            }
          }
        }
      }
      &-2 {
        .step-title {
          display: flex;
          justify-content: center;
          align-items: center;

          &:nth-child(1) {
            background-image: url(../media/img/step-2.png);
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            width: 250px;
            height: 250px;
            margin: auto;
            @include mq(desk-lg, max) {
              width: 200px;
              height: 200px;              
            }
          }
        }
      }
      &-3 {
        .step-title {
          display: flex;
          justify-content: center;
          align-items: center;

          &:nth-child(1) {
            background-image: url(../media/img/step-3.png);
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            width: 250px;
            height: 250px;
            margin: auto;
            @include mq(desk-lg, max) {
              width: 200px;
              height: 200px;              
            }
          }
        }
      }
      &-4 {
        .step-title {
          display: flex;
          justify-content: center;
          align-items: center;

          &:nth-child(1) {
            background-image: url(../media/img/step-4.png);
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            width: 250px;
            height: 250px;
            margin: auto;
            @include mq(desk-lg, max) {
              width: 200px;
              height: 200px;              
            }
          }
        }
      }
    }

  }

  .allies {
    
    padding: 18.5rem 0 3.5rem 0;
    @include mq(desk-md, max) {
      padding: 7.5rem 0 3.5rem 0;
    }

    // Waves

    background-image: 
      url(../media/img/second-wave-2.png),
      url(../media/img/second-wave.png);
    background-position: 
      0rem 1rem, 
      0rem -15rem;
      @include mq(desk-lg, max) {
        background-position: 
          0rem -2rem, 
          0rem -15rem;        
        }
        @include mq(desk-md, max) {
          background-position: 
            0rem -0rem, 
            0rem -10rem;
        }
    background-size: 
      100% auto,
      100% auto;
    background-repeat: 
      no-repeat,
      no-repeat;

    // Waves
    width: 100%;
    max-width: 100%;
    height: auto;
    max-height: 100%;

    &-title {
      text-align: center;
      padding: 2.5rem 0 6rem 0;
      h1 {
        font-size: 3.125rem;
        text-transform: uppercase;
        letter-spacing: 5px;
        color: $secondary-color;
      }
    }

    &-logos {
      text-align: center;
      align-items: center;
      .logo {
        color: $gray-01;
        background: $gray-04;
        margin: .75% auto;
        border-radius: 5px;
      }
    }

  }
  
}

.jumbotron {
  
  position: relative;
  background-color: $primary-color;
  width: 100%;
  min-height: 110vh;
  @include mq(phone-xl, max) {
    min-height: 100vh;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    background-image: url(../media/img/main-waves.png);
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 100% 25%;
    width: 100%;
    height: 100%;
    opacity: .6;
    z-index: 1;
  }

  &-title {

    text-align: center;
    
    .w-100 { filter: drop-shadow(0px 0px 60px rgba(255,255,255,0.075)) }

    h1 { 
      @extend %main-vera;
      @extend %flex-row-between;
      padding: 7rem 0;
      @include mq(phone-xl, max) {
        padding: 9rem 0 3.5rem;
      }
      span {
        max-width: 100%;
        font-size: 800%;
        @include mq(desk-md, max) {
          font-size: 600%;
        }
        @include mq(phone-xl, max) {
          font-size: 400%;
        }
      }
    }

    h2 {
      color: $white;
      font-size: 1.5rem;
      opacity: 1;
    }

    P {
      margin-top: 1rem;
      color: $white;
      letter-spacing: 2px;
    }

  }

}