footer {
  
  background: $secondary-color;
  height: auto;
  min-height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;

  * {color: $white}

  .row {
    align-items: center;
    .left {
      text-align: left;
      padding-left: 3rem;
      @include mq(desk-lg, max) {
        padding-left: .5rem;
        justify-content: center;
        text-align: center;
        margin: .5rem 0;
      }
    }
    .right {
      padding-right: 3rem;
      text-align: right;
      display: flex;
      justify-content: flex-end;
      @include mq(desk-lg, max) {
        padding-right: .5rem;
        justify-content: center;
        text-align: center;
        margin: .5rem 0;
      }
    }
  }

  .footer {
    &-copy {
      color: $white;
    }
    &-social {
      display: flex;
      * {
        margin: 0 .45rem;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    &-divider {
      height: 16px;
      border: 1px solid $white;
      margin: auto 1.45rem;
    }
  }

}