// --- Colors --- //

// Main colors
$primary-color: #1A0E3D !default;
$secondary-color: #8D4DA7 !default;
$tertiary-color: #B73ECE !default;

// Other Colors
$purple-dark-01: #1A0E3D !default;
$purple-dark-02: #232630 !default;
$purple-dark-03: #472D98 !default;
$purple-dark-04: #4F378D !default;
$purple-dark-05: #5F5967 !default;

// Element-colors
$button-color: #B73ECE !default;
$danger-color: #B10977 !default;
$bgbody-color: #F1F0F2 !default;
$inputs-color: #302253 !default;


// Grayscales
$gray-01: #0000005C !default;
$gray-02: #BFBDC6 !default;
$gray-03: #00000029 !default;
$gray-04: #f8f7f8 !default;

// Simples
$white: #fff;
$black: #000;


// --- Font-styles --- //

// Fonts
$futura: "futura-pt", sans-serif, serif !default;
$montserrat: "Montserrat", sans-serif, serif !default;

// Weight
$font-bold: 700 !default; 
$font-regular: 400 !default;
$font-light: 300 !default;

// --- Maps --- //

// Colors
$colors: (

  primary:    $primary-color,
  secondary:  $secondary-color,
  tertiary:   $tertiary-color,
  submit:     $button-color,
  danger:     $danger-color

);

// Breakpoints
$breakpoints: (

  "phone-xs":   320px,    
  "phone-sm":   375px,  
  "phone-md":   420px,
  "phone-lg":   520px,
  "phone-xl":   640px,
  "phone-xxl":  813px,

  "tablet-sm":  768px,
  "tablet-md":  1024px,
  "tablet-lg":  1224px,

  "desk-sm":    1140px,
  "desk-md":    1280px,
  "desk-lg":    1440px, 
  "desk-xxl":   1680px,

);
