.input-group {

  width: 85%;
  margin: auto;

  @include mq(phone-xl, max) {
    width: calc(100% - 1rem);
    margin: auto;
    text-align: left;
  }

  * { color: #a39db4}

  p {
    opacity: 0;
    font-family: $futura;
  }
  
  label {
    font-family: $futura;
  }
  
  input {
    margin: .5rem 0;
    padding: 10px 15px;
    background: $inputs-color;
    font-family: $futura;
    width: calc(100% - 2rem);

    border: {
      radius: 50px;
      width: 0px;
      // color: $white;
    }
    
  }
  
  &.is-invalid {
    label, p {
      opacity: 1;
      color: $danger-color;
    }
    p {
      margin-left: 1rem;
    }
    input {
      border: 1px solid $danger-color;
    }
  }

}